.wrapper {
  position: relative;
  height: 940px;
}
/* Title start */
@keyframes titleEnglish {
  0% {
    opacity: 1;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
  }

  50% {
    top: 0px;
    left: 75%;
  }

  100% {
    opacity: 0;
    top: 0px;
    left: 97%;
    font-size: 30px;
    color: #6a5af9;
  }
}
@keyframes titleJapanese {
  from {
    opacity: 0;
    font-size: 30px;
  }
  to {
    opacity: 1;
    font-size: 100px;
  }
}
.title {
  position: absolute;
  text-transform: uppercase;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 100px;
}
.title span {
  font-family: "Long Cang", cursive;
}
.japanese {
  color: #eee;
  font-size: 100px;
  font-family: "Kranky", cursive;
  animation-name: titleJapanese;
  animation-duration: 2s;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.english {
  color: #232323;
  font-size: 100px;
  font-family: "Shizuru", cursive;
  animation-name: titleEnglish;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
/* Title end */

