/* Message start */
.message {
  font-family: "Rock 3D", cursive;
  font-size: 60px;
  color: #55d2fc;
  text-align: center;
  width: 1000px;
  display: block;
  position: absolute;
  visibility: hidden;
  /* top: 30%;
  left: 50%;
  transform: translate(-50%, -50%); */
  animation-name: message;
  animation-iteration-count: 1;
  animation-delay: 4s;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  text-shadow: 1px 1px 1px rgb(238, 238, 238);
}
@keyframes message {
  0% {
    font-size: 0px;
    top: 0px;
    left: 0px;
    visibility: visible;
  }
  100% {
    font-size: 60px;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: visible;
  }
}
/* Message end */

/* Achieves start */
.achieves {
  display: inline-block;
  position: relative;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  padding: 10px 20px;
  padding-bottom: 100px;
  animation-name: achieves;
  animation-duration: 4s;
  animation-delay: 6s;
  animation-fill-mode: forwards;
  width: auto;
  height: auto;
}
@keyframes achieves {
  to {
    box-shadow: -10px -10px 30px 4px rgba(214, 164, 196, 0.7),
      10px 10px 30px 4px rgba(214, 164, 196, 0.7);
  }
}
.achieve {
  margin-left: -40px;
}
.achieve li {
  visibility: hidden;
  font-family: "Mochiy Pop P One", sans-serif;
  font-size: 25px;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: linear-gradient(to right, #5c527f, #6e85b2);
  animation-name: achieve;
  animation-duration: 1.5s;
  animation-delay: 7s;
  animation-iteration-count: 1;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
}
@keyframes achieve {
  from {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
/* Achieves end */
/* Last start */
.last {
  font-family: "Yuji Mai", serif;
  font-size: 60px;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;

  background-image: linear-gradient(95deg, #da0037, #e06785);
  margin-top: -10px;
  position: absolute;
  top: 790px;
  display: flex;
  opacity: 0;
  justify-content: center;
  align-items: center;
  animation-name: last;
  animation-duration: 3s;
  animation-delay: 8s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
@keyframes last {
  from {
    opacity: 0;
    left: 0px;
  }
  to {
    opacity: 1;
    top: 790px;
    left: 50%;
    transform: translateX(-50%);
  }
}
/* Last end */
